import { notFound } from "next/navigation";

type Creative = {
  id: string;
  type: string;
  media_url: string;
  click_url: string;
  duration: number | null;
  status: string;
  advertiser_email: string;
  created_at: string;
};

async function getCreative(id: string): Promise<Creative | null> {
  const res = await fetch(
    `${process.env.NEXT_PUBLIC_API_URL}/admin/creatives/${id}`,
    {
      headers: {
        Authorization: `Bearer ${process.env.ADMIN_TOKEN}`,
      },
      cache: "no-store",
    }
  );

  if (!res.ok) return null;
  return res.json();
}

export default async function CreativePage(props: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await props.params; // ← ВОТ ЭТО ГЛАВНОЕ

  const creative = await getCreative(id);

  if (!creative) {
    notFound();
  }

  return (
    <div style={{ maxWidth: 900, margin: "0 auto", padding: 24 }}>
      <h1 style={{ fontSize: 28, marginBottom: 16 }}>
        Creative review
      </h1>

      {creative.type === "video" && (
        <video
          src={creative.media_url}
          controls
          style={{
            width: "100%",
            borderRadius: 12,
            marginBottom: 20,
          }}
        />
      )}

      <div style={{ marginBottom: 20 }}>
        <p><b>Status:</b> {creative.status}</p>
        <p><b>Advertiser:</b> {creative.advertiser_email}</p>
        <p>
          <b>Click URL:</b>{" "}
          <a href={creative.click_url} target="_blank">
            {creative.click_url}
          </a>
        </p>
      </div>
    </div>
  );
}
